import React, { useEffect, useState } from 'react'
import logo from '../../images/logo.png' 
import {Link} from 'react-scroll'

import './Navbar.css';
const Navbar = () => {
  const[scroll,setScroll]=useState(false)
  const [isOpen, setIsOpen] = useState(true);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  useEffect(()=>{
    
    const handelScroll =()=>{
      if(window.scrollY > 50)
      {
        setScroll(true);
        console.log(scroll)
      }
      else{
        setScroll(false);
        console.log(scroll)
      }
    };
    window.addEventListener('scroll',handelScroll);
    return ()=>{
      window.removeEventListener('scroll',handelScroll);
    }
  },[]);
  return (
    
      <div className={`navbar ${ scroll ? 'scroll' : ''} `}>
       <div className="logo">
        {/* <img src={logo} alt="" /> */}

        <h1>JK</h1>
       </div>
        <ul  className={`menulist ${isOpen ? 'visible' :''}`}>
            <li><Link className='ip' to="home" smooth={true} duration={500} onClick={toggleMenu}>Home</Link></li>
            <li><Link  className='ip' to="About" smooth={true} duration={500} onClick={toggleMenu}>About</Link></li>
            <li><Link  className='ip' to="Service" smooth={true} duration={500} onClick={toggleMenu}>Service</Link></li>
            <li><Link  className='ip' to="project" smooth={true} duration={500} onClick={toggleMenu}>Project</Link></li>
            <li><Link  className='ip' to="Contact" smooth={true} duration={500} onClick={toggleMenu}>Contact</Link></li>
        </ul>
        <div className='menu-icon'>
          <i className={` ${isOpen ? 'fa-sharp fa-solid fa-bars' :'fa-solid fa-x'}`} onClick={toggleMenu}></i>
        </div>
    </div>

  )
}

export default Navbar